import { wrapTextNumber } from '../../../../utils/String.js'
export default {
  name: '',

  data() {
    return {
      wrapTextNumber,
      valueList: [],
      number: 15
    }
  },
  methods: {
    reFindStudentList() {
      this.$callApi({
        api: 'growth_growth_listByStuid',
        param: {
          campusid: 1615,
          growthid: 'e62e2665745daecd0c2ba791d3bd5699',
          stuid: 1561247441
        }
      }).then(data => {
        this.valueList = data
      })
    }
  },
  created() {
    this.reFindStudentList()
  }
}



